.modal-header {
    display: flex;
    flex-shrink: 0;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 1rem;
    border-bottom: 1px solid #f0b032;
    border-top-left-radius: calc(0.3rem - 1px);
    border-top-right-radius: calc(0.3rem - 1px);
    text-align: center;
}
.modal-body {
    position: relative;
    flex: 1 1 auto;
    padding: 1rem;
    max-width: 100%;
    
}


